import { useNavigate } from "react-router-dom";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";


const Inicio = () => {
  const navigate = useNavigate();

  const cerrarSesion = () => {
    localStorage.removeItem("token");
    navigate("/login");
  };

  return (
    <div
      style={{
        fontFamily: "Arial, sans-serif",
        padding: 0,
        margin: 0,
        height: "100vh",
        display: "flex",
        flexDirection: "column",
      }}
    >
      {/* Header */}
      <header
        style={{
          width: "100%",
          backgroundColor: "#333",
          color: "#fff",
          textAlign: "center",
          padding: "10px 0",
          zIndex: 2,
        }}
      >
        <h1>Bienvenido al sistema</h1>
      </header>

      {/* Contenedor del video y contenido */}
      <div
        style={{
          position: "relative",
          flex: 1, // Hace que ocupe el espacio restante de la pantalla
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {/* Video de fondo */}
        <video
          autoPlay
          loop
          muted
          poster="https://sermex0.github.io/Sermex_Api_Images/sermex_img-video.png"
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            objectFit: "cover",
            zIndex: -1,
          }}
        >
          <source
            src="https://sermex0.github.io/Sermex_Api_Images/SERMEX-video..mp4"
            type="video/mp4"
          />
        </video>

        {/* Contenido centrado sobre el video */}
        <div
          style={{
            textAlign: "center",
            backgroundColor: "rgba(0, 0, 0, 0.5)",
            color: "#fff",
            padding: "20px",
            borderRadius: "10px",
            zIndex: 1,
          }}
        >
          <h2>Bienvenido a la pantalla de inicio 🚀</h2>
          <button
            onClick={cerrarSesion}
            style={{
              padding: "10px 20px",
              fontSize: "1rem",
              cursor: "pointer",
              backgroundColor: "#ff5733",
              color: "#fff",
              border: "none",
              borderRadius: "5px",
              marginTop: "15px",
              transition: "background-color 0.3s ease",
            }}
            onMouseOver={(e) => (e.target.style.backgroundColor = "#ff4500")}
            onMouseOut={(e) => (e.target.style.backgroundColor = "#ff5733")}
          >
            Cerrar Sesión
          </button>
        </div>
      </div>

      {/* Footer */}
      <footer
        style={{
          width: "100%",
          backgroundColor: "#333",
          color: "#fff",
          textAlign: "center",
          padding: "10px 0",
        }}
      >
        <p>© 2025 Mi Aplicación. Todos los derechos reservados.</p>
        
        <Box
    sx={{
      display: 'flex',
      justifyContent: 'center', // Opcional: centra las imágenes horizontalmente
      gap: '10px', // Espaciado entre imágenes
    }}
  >
      {/* iconos de empresas socias  */}
    <Avatar
      alt="Icono sin redondear"
      src="/images/vicon.png"
      sx={{ borderRadius: 0, width: 100, height: 48 }}
    />
    <Avatar
      alt="Icono sin redondear"
      src="/images/eagle.png"
      sx={{ borderRadius: 0, width: 100, height: 48 }}
    />
    <Avatar
      alt="Icono sin redondear"
      src="/images/od.png"
      sx={{ borderRadius: 0, width: 100, height: 48 }}
    />
    <Avatar
        alt="Icono sin redondear"
        src="/images/density.png"
        sx={{ borderRadius: 0, width: 100, height: 48 }}
      />
    <Avatar
        alt="Icono sin redondear"
        src="/images/milesight.png"
        sx={{ borderRadius: 0, width: 100, height: 48 }}
      />
    <Avatar
        alt="Icono sin redondear"
        src="/images/flir.png"
        sx={{ borderRadius: 0, width: 100, height: 48 }}
      />
    <Avatar
          alt="Icono sin redondear"
          src="/images/ceia.png"
          sx={{ borderRadius: 0, width: 100, height: 48 }}
        />
  </Box>
 

      </footer>
    </div>
    
  );
  
};

export default Inicio;
